      <p class="pt-2 pb-4"> <strong>British Museum 1849,0315.10. Dimensions of original: 290 mm x 368 mm.</strong></p> 
        <span class="float-left pr-6 pb-2"><img :src="thumbnail" width="200"/> <a href="#" @click="getlargerImage()" class="underline font-bold" >View larger image</a></span> 

        <p class="pb-2">The state of international politics in the early Jacobean era was summarised in an undated, cartoon-like print engraved by Thomas Cockson, entitled <span class="italic">The Revells of Christendome</span>. It represents an English view of the settlement between Spain and the States General of Holland, at a time when England and France were negotiating peace between the two parties. F.G. Stephens, who first suggested the plausible date 1609 for the print, noted that 'This Peace [i.e. The Twelve Years Truce] was a severe blow to the Pope, and Maurice [of Nassau, Prince of Orange] was watching the game which promised so much benefit to his country'.    </p>

        <p class="pb-2">Stephens thought <span class="italic">The Revells</span> 'probably of German origin'a strange attribution, given the Netherlandish context he suggested. In fact, what would appear to be the original model for Cockson's print can now be identified as one bearing the monogram of the Netherlandish engraver Pieter van der Heyden/Petrus a Merica with text in Dutch, which opens <span class="italic">Hola ghy roouers</span>. Harms believes internal evidence suggests a date of issue of between 1598 and 1600,   though I note that in Hind's <span class="italic">History of Engraving</span>, Van der Hetden's <span class="italic">floruit</span> is given as 1550-70, though the British Museum database confirms he was active beyond 1584</p>

        <p class="pb-2">
          Comparison with his source shows that Cockson has retained the likenesses on the Catholic side (except that he has replaced a Merica's cardinal's hat with a Jesuit's bonnet), but substituted clearly recognisable portraits on the Protestant side: James I of England, Henri IV of France (d.1610), Christian IV of Denmark, and <span class="italic">Grave Maurice</span> of Nassau, Prince of Orange. They are depicted seated round a table playing various games of chance (backgammon, cards and dice) against three Catholic monks who have stakedand losta monstrance, a chalice and much coin. In a picturesque asidenot in his originalCockson has added a dog urinating on the foot of one of the monks.
        </p>
 
        <p class="pb-2">How can we be certain of the publication date of this undated print? The portrait of James I is unmistakable, so provides us with a <span class="italic">terminus post quem</span> of 1603. If the seated figure next to him is correctly identified as Henri IV of France, then we have a <span class="italic">terminus ante quem</span> of 1610in such a context and given the suspected pun on Pax, for the Peace of the Twelve Years Truce, 1609 seems a reasonable guess. The engraving is signed by Cockson whose dates, however, are not helpful (active 1591-1630).</p>

        <p class="pb-2">The bonneted figure behind the Pope is problematic: Stephens implies he is the Cardinal Infante Ferdinand, i.e. the younger son of Philip III of Spain and Margaret of Austriaand he would be an excellent fit, having Austrian, Spanish and Netherlandish connexionsbut is, sadly, ruled out of court by chronology, being born in 1609! The verses, however, describe the standing figure beside the Pope as both a cardinal and an Austrian perhaps some reader can enlighten me as to his identification? </p>

        <p class="pb-2">The English verse styles the monstrance on the table a <span class="italic">pax</span>, for the benefit, I suggest, of the pun on the <span class="italic">Peace</span> or Truce, which is the real subject of the conference at the table. The verse writer has done his best to wring several such puns out of the composition (see below for full transcription of text): e.g. in their card game, <span class="italic">the Monck could show France nothing but the Knave</span>. The verses explicitly state that Henri is playing maw, a game we are no longer familiar with (and was perhaps only known in the British Isles?), but close comparison with the cards of his source, shows that Cockson has made some subtle alterations to his originalfor the card shown face-up on the top of the deck on the table is not Van der Heyden's Ten of Hearts, but the five of that suit, and as hearts are trumps, it is the five of trumps, a special card in the game of maw known by the term 'five finger' (see verse). Furthermore, in Cockson's version, the two uppermost cards in the monk's hand, as if he is about to play one of them, are the Knaves of Diamonds and Hearts (in Van der Heyden's original, they are the Seven of Diamonds and King of ?Hearts), while his opponent, Henri IV, is already flourishing the Ace of Hearts.</p>

        <p class="pb-2">
          Not all the rules of maw have come down to us, though we do have a single sheet of <span class="italic">The Groome-porters lawes at Mawe, to be observed in fulfilling the due orders of the game</span> (c.1600) which prints sixteen rules, thoughthere being no mention of the <span class="italic">five-finger</span>, for instancethere must surely have been further sheets which have not survived. Two of the extant sixteen <span class="italic">lawes</span>, however, concern the <span class="italic"></span>Ace of Hartes, which clearly played a significant role in the gamewhichever suit was trumps, cf. <span class="italic">Like the ace of hearts at Mawe</span> in <span class="italic">History of Pope Joan</span> (1599).   By good fortune, however, we learn from <span class="italic">Greene's Newes from Heaven & Hell</span> (1593) that, <span class="italic">Although the knave of trumpes be the seconde carde at Mawe, yet the five-finger may commaunde both him and all the rest of the pack</span>.
        </p>

        <p class="pb-2">
          If I understandor, rather, inferthe rules of the game correctly, the three highest value cards in the game were thusin descending orderFive of Trump Suit, Knave of Trump Suit, Ace of Trump Suit, and thusin the present contextFive of Hearts, Knave of Hearts, Ace of Hearts. France has cut the pack and 'shown' the Five of Hearts, and as Hearts are trumps, this is the unbeatable <span class="italic">Five Finger</span>  horribly frustrating for the Monk who has what is the second-highest card possible, the Knave of Trumps (Hearts) in his hand! Henry also holds (and shows) the third-highest card, the Ace of Trumps (Hearts).
        </p>

        <p class="pb-2">
          Here is the relevant portion of the verse caption to our sheet:
        </p>

        <p class="pb-2">
          ... France setts gould,<br>
          & shuffles: the Monk cutts: but France (being bould)<br>
          Deales freely: Rubs : and the first card hee showes,<br>
          is the Five Finger, which being tourn'd up, goes<br>
          Cold to the Moncks hart : the next card, France sees<br>
          in his owne hand, is the Ace of hartes, "I Leeze"<br>
          Cryes out the Monck; sayes France, "Show what you have,"<br>
          the Monck could show France nothing but the Knave.
        </p>

        <p class="pb-2">(For full text of the verses, see below.)</p>

        <p class="pb-2">In a reversed copy of the present sheet, dated conjecturally '1626' (by Stephens   ), and which bears no engraver's signature nor other imprint information, but keeps the urinating dog and the verses (except for necessary changes in the names of the participants as noted below), the cardinalwearing the characteristic broad-brimmed hat now, not the bonnetmay represent Richelieu (despite the verses which remain unchanged and still mention <span class="italic">Austria</span>), the English king James I (d.1625) is replaced by Charles I, Henri IV of France (d.1610) by Christian IV of Denmark, and Christian IV of Denmark of the earlier version by Gustavus Adolphus, King of Sweden (1611-1632), while the Transylvanian war-leader Bethlen Gabor replaces Maurice of Nassau, Prince of Orange (d.1625). What can we say for certain about the date of this later version? The unmistakable likeness of the youthful crowned Charles I replacing James, provides a <span class="italic">terminus post quem</span> of 1625, and the distinctive profile head of Bethlen Gabor provides the <span class="italic">terminus ante quem</span>, as he died in 1629.</p>

        <p class="pb-2">Curiously, the engraver of this later, reversed image seems to have reverted to the cards of the original van der Heyden version, i.e. the card turned up on the table is certainly the Ten of Hearts, and the cards in the monk's hand are the Five(?) of Diamonds, and a court cardthis, of course, makes a nonsense of the details of the game of maw as given below the image, which remain the same as in Cockson's version, but for the substitution of <span class="italic">Denmarke </span>for <span class="italic">France</span> in the relevant stanza.</p>

        <p class="pb-2">Interestingly it is the impression of this later version of the composition (in the Society of Antiquaries collection; another impression is in the Department of Prints & Drawings of the British Museum) which is the subject of what must be one of the earliest journal articles to concern a print, <span class="italic">A Political Caricature, temp. Charles I</span>, in <span class="italic">The Gentleman's Magazine</span> of July 1853. A similarly reversed copy of Van der Heyden's original composition, which I have not seen, was issued in Germany in 1633</p>

        <p class="pb-2">It seems as though an even later version was published c.1660  if we can believe the estimate of the cataloguer on whose telegraphic description we are dependent.  It bears the same title but now the verses are disposed in five columns and the revellers presumably updated to reflect the political circumstances of a later seventeenth-century Europe. No imprint is mentioned, so we may assume there is none.  </p>

        <p class="pb-2"><span class="italic font-bold">The Revells of Christendome</span> (1609)</p>

        <p class="pb-2">
        Greate Brittaine with proud Rome at Tables playes,<br>
        Rome looseth every Stake that downe shee layes,<br>
        yett fretts and sweares to win all, tho' shee pawne<br>
        her Relliques. thos shee setts, & thos are drawne.<br>
        The last Stake is the Pax : Greate Brittaines hand<br>
