        <p class="py-2"><span class="italic">The Committee; or Popery in Masquerade</span>, by an unknown engraver, is a most accomplished print <span class="italic">satyr</span>as the accompanying text interestingly terms itissued in April 1680, which <span class="italic">inter alia</span> holds up the various nonconformist sects for ridicule. Here representatives of the various denominations of the earlier Commonwealth eraa naked Adamite, a Ranter, a Quaker, an Anabaptist, a Presbyterian, an Independent, [Lodowick] <span class="italic">Mugleton</span> himself, and [James] <span class="italic">Nailor</span>are depicted as constituting a committee listening to petitions from a dog, a horse, a man and a woman. In the lefthand corner, the various victims of the Civil War appear in chains, while in the righthand corner, a Church of England parson is forced to vomit forth his livinga detail which may well owe something to <a href="">Doctor Panurgus</a>, which Sir Roger L'Estrange, who conceived the design for the present print and penned its text, had accorded his <span class="italic">imprimatur</span> eight years earlier. <sup>ii</sup>  At this period, L'Estrange was the most effective of the Tory propagandists, and the message of the print as a whole is to imply that the present day Puritan faction is plotting to seize Parliament and subvert the Government, which will lead to a second Civil War. When criticised for his part in the design, however, L'Estrange declared that the print was <span class="italic">a piece historical and only recommended by way of caution</span>. <sup>iii</sup>  The subtitle alludes to the present era of Popish Plot(s) and the verses describe how such misinformation is to be  </p>

