         <h3 class="py-6 text-3xl">A Mappe of the Man of Sin</h3>
         <h4 class="py-2 text-xl">No.22  August 2008</h4>
     </div>
     <div class='relative text-left overflow-y-auto'>
       <p class="py-2"> <strong>Rare Book Division, Department of Rare Books and Special Collections, Princeton University Library. Shelfmark:</strong> </p>
       <p class="pt-2 pb-4"> <strong>(Ex) BT985 .W5e. Dimensions of original: 443 mm x 545 mm.</strong></p> 
         <span class="float-left pr-6 pb-2"><img :src="thumbnail" width="200"/> <a href="#" @click="getlargerImage()" class="underline font-bold" >View larger image</a></span> 
 
         <p class="pb-2">From early in the Reformation the Pope was identified with the 'man of sin' spoken of by St Paul in his Second Epistle to the Thessalonians 2:3-4, and thus regularly identified in Protestant polemic with the Antichrist. According to Paul, on the Last Day will be revealed 'that man of sin ..., the son of perdition; Who opposeth and exalteth himself above all that is called God, or that is worshipped; so that he as God sitteth in the temple of God, shewing himself that he is God'. In the year following publication of the present sheet, one I.P. composed a tract against the Anabaptists entitled, <span class="italic">Anabaptismes mysterie of iniquity unmasked... also wisedomes bountie unmasking the man of sinne</span> (1623), but use of the phrase was a commonplace of Protestant rhetoric, as seen, for instance, in John Rainolds' sermon, <span class="italic">The discovery of the man of sinne: wherein is set forth the changes of Gods church, in her afflictions by his raigne. Consolations by his ruine</span> (1614). Subsequently, in the magnificent engraved frontispiece to 'The Second Tome' of William Prynne's <span class="italic">Exact Chronological Vindication...</span> (1665), St. Paul himself identifies the Pope (depicted here with triple crown falling from his head) as the Man of Sin. <span class="italic">The man of sin: or a discourse of Popery: wherein the numerous and monstrous abominations... of the Romish church are by their own hands exposed...</span> by William Hughes appeared in 1677, and, at the time of the Popish Plots, the motif is pressed into service yet again, specifically, in the seventh pageant of <span class="italic">The Solemn Mock Procession...</span> (1680), which, according to the 'Explanation' beneath the print, represented 'the Man of Sin himself on a Throne, with his Counsellor the Devil inspiring of him, what new Artifice of cruelty must come next' </p>
 
         <p class="pb-2">The present <span class="italic">Man of Sin</span>, ESTC S96046,   is a magnificent engraved broadside which survives uniquely in Princeton University Library. The <span class="italic">Stationers Register</span> entered it to John Bellamie on 6 July 1622, as <span class="italic">A picture called the Man of sinne revealed or a Map of the kingdome of Antichrist and the Ruyns thereof</span>, and it does indeed bear the imprint, 'Are to be sould by Iohn Bellamie at his shop at the 2 Grey hou[n]ds in Cornehill neer thexchang'. The text is signed 'W.G. scripsit et trans.', who has plausibly been identified as William Gouge, 'arch Puritan preacher at Blackfriars and chronicler of the 1623 Fatal Vespers' (referring to the collapse of a chapel in Blackfriars at which a prominent Jesuit was preaching).   On the other hand, I do not know what W.G. was translating, even though the word must surely imply a foreign original for the whole design.</p>
 
         <p class="pb-2">
           The twenty lettered constituents of the composition are keyed to a verse text, as well as being provided with banderole inscriptions and Biblical references. (For full transcription and commentary, see below.) Figure <span class="font-bold">A</span> is the 'sly impostor' who aspires to tread on Mount Sion (labelled <span class="font-bold">B</span>), on whose summit is a sunburst bearing the Tetragrammaton. He is dressed in 'menstruous rags' according to the caption (or 'polluted clouts', as the verse styles them), carries a crosier, and shields his eyes from the sudden increase in the power of the sun's rays, augmented from a vessel held by an angel (labelled <span class="font-bold">C</span>) despatched from heaven. The excess of light blinds him ('blind this Blinder') and 'shews hid [his] hipocrisies', as with the end of his crosier, figure <span class="font-bold">D</span>, that 'Unerring Man'used ironically as a satirical glance at the claimed papal infallabilitymuddies the waters of 'sweet Siloes streaming foard' (which issue from a classical anthropomorphic fountain, also labelled <span class="font-bold">D</span>), and as figure <span class="font-bold">E</span>, forces the laity to drink the 'stir'd dregs'. Figure<span class="font-bold">D</span> is also shown with a church on each shoulder, as is figure <span class="font-bold">F</span>, his 'Abby-lubber Preest', who holds a further two churches in his hands, which he and figure <span class="font-bold">G</span>, 'Gold-linde Simony' (with a church on one shoulder and a bag of money in his left hand),   offer to the large central figure, labelled <span class="font-bold">H</span>.
         </p>
 
         <p class="pb-4">Behind Simony, a simoniac prelate (labelled <span class="font-bold">I</span>) vomits out churches of various sizes that he has previously devoured, and behind him an impressive complex of buildings, labelled <span class="font-bold">K</span>, is presumably intended for the Vatican, from the highest tower of which flutters a banneret inscribed with the Latin and English texts of I Peter 1:24: 'These all must passe, Like flower of Grasse.' Adjacent to it is an equally impressive cathedral labelled <span class="font-bold">L</span>, 'Gods Temple', which a ray from 'Heav'ns jealous Eye' (<span class="font-bold">M</span>) pierces. In the top right hand corner of the sheet, is the Tower of Babel (<span class="font-bold">O</span>) starting to collapse, and in the act of falling from its pinnacle is a naked woman (labelled <span class="font-bold">P</span>) with a goblet in one hand and a mirror in the other, the papal tiara having already fallen from her head; the verse identifies her as 'th'Abominable Whoore', and mentions her 'Cup of Witchcrafts' and her 'Crowne'. Her caption is a verse from Isaiah which alludes to the fall of Babylon, and a recumbent bishop (labelled <span class="font-bold">Q</span>) at the base of the Tower, still holding his church, laments, 'Alas Alas Babilo[n] that great Cit[y]' (Revelation 14:8)there is no doubt that we are to identify her as the Apocalyptic Whore of Babylon. Despite their differing vowel quality in modern English, 'Babel' and 'Babylon' are the same word (from Hebrew 'babel') and, as the <span class="italic">Oxford English Dictionary</span> notes, the name of the city was also used for 'the mystical Babylon of the Apocalypse; whence, in modern times, [it was] applied polemically to Rome or the papal power'. As the text makes clear, the ruin of Babel/Babylon is also the ruin of 'her Rich lovers', including the 'Emperours, kings and Preests', here labelled R, whose crosiers and sceptres are indeed depicted 'slent' [broken]. The engraver has also shown 'The Chair orturnd', doubtless intended to be the papal throne. </p>
 
         <p class="pb-2">
           To the left, labelled <span class="font-bold">S</span>, is heaven's gate which is guarded by the symbols of the Four Evangelists and from which 'proceede lightening and thunder'; despite the number of churches the bishop (whose crosier falls apart) has brought with him, he and his fellows are shown being repulsed. The palatial Heavenly City, labelled <span class="font-bold">T</span>, is surmounted by another blazing sun bearing the Tetragrammaton. Curiously, placed outside the confines of the City, the 'Redeemed' (labelled V), 'Palmes in their hands', harp and sing in honour of the Deity. <span class="font-bold">A, D,E</span> and the 'Manne' all wear soft squareish caps which are doubtless intended to resemble the type worn by Jesuits.
         </p>
 
         <p class="pb-2">Several figures in <span class="italic">A Mappe of the Man of Sin</span> are depicted with one or more churches on their shoulders, but this particular motifthe pluralist who holds two or more benefices at the same timewas a recurring and, in fact, quite non-denominational complaint. It is found illustrating the title-pages of two pamphlets issued in 1642, the anonymous <span class="italic">A Purge for Pluralities, shewing the unlawfulnesse of men to have two Livings. Or The Downe-fall of Double Benefices</span>, in which the relatively crude woodcut depicts a minister bearing churches on both shoulders and one in his right hand, and Blaxton's <span class="italic">A remonstrance against the non-residents of great Brittaine</span>, in which the accomplished engraving entitled, <span class="italic">The Carelesse Non-resident</span>, shows a minister bearing a church on his right shoulder and holding another in each hand. In 1681 Samuel Hieron's <span class="italic">The Protestant Mirrour...or, The careful resident, and the careless non-resident</span> was issued in the form of a single-sheet broadside headed by engraved portraits of the two types of clergyman: the 'Carefull Resident' bearing one church on his right shoulder, the 'Careless Non-Resident' having one in each hand and a third on his head. Issued in 1622, the present sheet is the earliest instance of this pluralist motif known to me, unless the puzzling <a href="/printsInFocusView/6" class="text-blue-600 dark:text-blue-500 hover:underline" target="_blank">Doctor Panurgus</a> sheet engraved by Martin Droeshout (1601-after 1639) and most recently dated to the 1620s by Antony Griffiths,   is earlier.</p>
 
         <p class="pb-2 font-bold">A Mappe of the Man of Sin (transcription)</p>
 
         <p class="pb-2">A Mappe of the Man of Sin: wherin is most lively delineated the Rising Raigning and Ruine of the Kingdome of Antichrist Rising by Pride shutting the Booke of God and imposing Huma[n]e traditions on mens Consciences Raigning by Avarice, Simony, Superstition, Pleasure, and Hipocrisie. Ruined by Gods Spirit casting downe the Babell of their Abo[m]inations, & discoveri[n]g their da[m]nable Actions. All gathered out of the expresse word of God. </p>
 
         <p class="pb-2">
         From auntient dayes Sins Misterie gan worke<br>
         Which did in Error-led Professors lurke.<br>
         Faind Faith, hells instrument, sins proper cell,<br>
         This Babels ground work laid: when thus befell;<br>
         <span class="font-bold">A</span> That sly Impostor, under glozed stile<br>
         Of Holines, with Humane Doctrine vile,<br>
         Cast   to obseure [sic] the Gospels light; which done,<br>
         His rule ore all the Universe might run;<br>
         And weening t' set unhallowed feet, upon<br>
         <span class="font-bold">B</span> Sacred Mount Sion, Gods Delight, Gods owne;<br>
         The place reservd his Chosen flocke to feed<br>
         <span class="font-bold">C</span>Power on the Sun was pourd (Heavens king decreed)<br>
         To blind this Blinder, and his deeds descry.<br>
         And's Righteousnes, wheron he did rely;<br>
         These then appearing at the best but as<br>
         Polluted clouts wherewith he clothed was.<br>
         <span class="font-bold">D</span> This that Unerring   Man, who leaves the Word,<br>
         Wisdomes fayre Fount, sweet Siloes streaming foard,<br>
         <span class="font-bold">E</span> Drinking stir'd dregs, and forcing others soe,<br>
         Willfull to hast his owne and others woe<br>
         But Careles to cut short, or curbe at least<br>
         <span class="font-bold">F</span> (Yea guerdoning) each Abby-lubber   Preest;<br>
         Whos Lucer   lets him not Christs Church up-build,<br>
         Busied to reape the Gaines his churches yeild.<br>
         <span class="font-bold">G</span> And Gold-linde   Simony there there stands in grace,<br>
         To get desertles Hierlings higher Place:<br>
         The Devils goatheards; ravening Theuees [sic] that venter<br>
         To clime the fold, but not the right Dore enter:<br>
         <span class="font-bold">H</span> The Universall Vicar will dispense<br>
         With these (for th' Churches state) without offence;<br>
         His Eye of Judgment darkned to discearne<br>
         His trusted charge, & what might truth concer[n]e:<br>
         False AntiChrist, an Idole shepheard hight,<br>
         Seeking his flocks fleece, not their benifit.<br>
         <span class="font-bold">I</span> But lett them heape on Wealth to Wealth; Gods power<br>
         Makes them cast out what (soe) they doe devoure,<br>
