         <span class="font-bold">L</span> For having made this Hallowed House of prayre<br>
         A Thevish Den, by lawlesse actions there<br>
         <span class="font-bold">M:</span> The while Heav'ns jealous Eye beholds each thing;<br>
         <span class="font-bold">N.</span> And sends a venging Sword, swift plagues to bring,<br>
         Showld breake the strength of that proud Man of Sin<br>
         <span class="font-bold">O.</span> With this; the Babell wher their trophies been<br>
         Gods furious Wrath for ever casts to ground<br>
         And doth th'Abominable Whoore confound<br>
         <span class="font-bold">P.</span> Whose Cup of Wichcrafts, and her Crowne orethrowne,  <br>
         <span class="font-bold">Q</span> Cause her Rich lovers mazedly   to mone.<br>
         <span class="font-bold">R</span>Her Ruine ruinng [sic] Emperours, kings and Preests<br>
         Made Sacrilegious, by her Hellish hests:<br>
         The Chair orturnd, their Croziers, Septers slent;  <br>
         And they (deservdly) in darcke Horror pent.<br>
         <span class="font-bold">S</span> No Merits may obtaine Heavns entry for them;<br>
         For those Angellicke ministers abhorr them.<br>
         <span class="font-bold">T</span> Gods Citty is reservde for such as trust<br>
         Not in their owne works, but on Christ the just<br>
         <span class="font-bold">V</span> Wher his Redeemed prayse with endles joyes  <br>
         Him that saves His, and thus his foes destroyes
         </p>
 
