     <p class="pb-2">
      One particularly venerable constituent reversal of the <span class="italic">monde renvers</span> topos, that of the cats hunted by the rats, early achieved independence as a subject in its own right -- as early as the 12th century mural painting in the church of Purgg in Austria --  in the form of <span class="italic">The Cats Castle Besieged and Stormed by the Rats</span>. As a print, it was popular throughout Europe, from as early as c.1500 in Germany, <sup>i</sup> but the earliest known English example to survive was issued some time after c.1665, <span class="italic">qrinted</span> [sic] and <span class="italic">Sould by John Overton</span>, though it does not appear in any of Overton's published catalogues; it is preserved uniquely in the Department of Prints and Drawings at the British Museum. A very closely-related version, but without any imprint, is held in the Library of Congress collection.</p>
        
      <p class="py-2">Without attempting a comprehensive listing, subsequent eighteenth-century versions include the 'wood royal' <span class="italic">Cat's Castle, besieged and stormed by the Rats</span> recorded in Marshall and Dicey's '1764' catalogue; <sup>ii</sup> <span class="italic">Cats castle besieged by the rats</span> issued by John Marshall c.1790 (Bodleian); another of c.1790 by Bowles & Carver, and a version closely related to this, bearing the imprint of G (?) Ash of Fetter Lane, London (British Museum), and yet another example issued by G. Sheppard in the Lewis-Walpole Library, Farmington, Connecticut. </p>

      <p class="py-2">The present Overton sheet dramatically presents the feline fortress centre-stage, with flags flying from its turrets, a bell tower and a cupola with a rat being hanged from a protruding mast. In it stands <span class="italic">Maister Tybet [sic] prince of Cat[s]</span>, who proclaims: </p> 
      <pre>
