      <p class="py-2">This literary humour is reflected in details of the image  the rats' banners displaying cheeses and hams, while the flags flown from the cats' castle bear fish (three fish in the earliest-known German woodcut of c.1500, and also in the late seventeenth-century English version where they appear above the legend <span class="italic">Herrings for ever</span>, while the ensign which flutters from the stern of <span class="italic">The Royal Rat</span> depicts two rats either side of a large circular cheese over the motto, <span class="italic">Cheshire Cheese for ever</span>).</p>

      <p class="py-2">The names of the two battle-leaders on the present sheet perhaps suggest derivation from some French version (<span class="italic">Tybert</span>spelled correctly in the Library of Congress impressionand <span class="italic">Nigro Musell</span>, perhaps Black Snout, the latter element from Old French <span class="italic">musel</span>, snout), and yet in an early seventeenth-century French version of <span class="italic">La grande & merueilleuse Bataille, d'entre les Chats, & les Rats</span>, which bears the imprint <span class="italic">A Lyon pour Leonard Odet; au coin de rue Ferrandiere</span>, 1610, the prince des Chatz is named <span class="italic">Mitou</span>, while in a later seventeenth-century version, closer in composition to the present example, issued by Pierre Bertrand in Paris and entitled <span class="italic">Le Fort des Chatz assiege par les Ratz</span>, he is named <span class="italic">Raminagrobis </span>(probably the source of the name of La Fontaine's feline hero). It is curious that it is the English sheet rather than the French one which uses the name <span class="italic">Tibert</span> for the cats' leader. Deriving from the name given to the cat in the medieval French beast-epic, the <span class="italic">Roman de Renart</span>, it was certainly familiar to the Elizabethans and lies behind Mercutio's punning reference to <span class="italic">Tybalt</span> as <span class="italic">King of Cats in Romeo & Juliet</span> (III.i.76), and Nashe's contemporary reference to <span class="italic">Tibault...Prince of Cats</span> in <span class="italic">Have with you to Saffron Walden</span> (1596). By the time of the late eighteenth-century English sheets, the protagonists are the less colourfully-named <span class="italic">King Mew</span> and <span class="italic">Prince Squeak</span>.</p>

      <p class="py-2">Though all European versions of the print bear a close generic resemblance, telltale details in the English version suggest it derives immediately (in reverse) from a Dutch model. <sup>iii</sup>  </p>

      <p class="py-2">The originators of the prints have left the siege in the balancefrom the images it is not possible to guess the outcomebut the lengthy verse text accompanying the Lyon version, at least, makes it clear that the cats, who are explicitly said to represent great villains <span class="italic">[gros larrons]</span>, successfully defend the castle and the few surviving rats, representing the lesser villains <span class="italic"></span> are forced to flee. The earliest known version similarly leaves the outcome of the siege undecided, but we are given some orientation as to how to read this provocative image from the couplet</p>

      <pre>
      disi figur get alle di an
      dy iren obristen under sta[n]
      </pre>

      <p class="py-2">This image refers to all those who are subject to their superiors</p>

      <p class="py-2">Though even this in its ambiguitytautology, indeed!gets us no further, though German commentators have noted the popular peasant movements in late fifteenth-century Germany associated with such names as the <span class="italic">Bundschuh</span>, and a little later, <span class="italic">Armer Konrad</span>. At the time our present print was issued, the tragic upheaval of the English Civil War was still fresh in the memory, and it is quite possible that an earlier (lost) versionor, just possibly, the very version without imprint in the Library of Congresshad been issued in the 1630s or 1640s. But while there is some reason to believe that the rats storming the cats' castle was sometimes capable of a political interpretation, it seems that for the majority of viewers most of the time it was a purely humorous image.</p>

      <p class="py-2">The Bowles & Carter version, London, c.1790, has verse captions as follows:</p>

