        <p class="pb-2">The subject of this extraordinary sheet is perhaps in essence a 'complaint on the times', a satire of universal folly in which a tripartite division of the realm into <span class="italic">Cuntry, Citty & the Court</span> is symbolised, respectively, by <span class="italic">rude Rusticall</span> being purged by the doctor on the close-stool, <span class="italic">spruce master Cittyzsinne</span> standing behind the Doctor, and the <span class="italic">Gallant</span> (i.e courtier) whose head is just entering the subliming furnace. But as the young man, prey to multifarious follies and devoted to fashionable fads and fancies, has long been the target of the moralists' especial wrath, and the <span class="italic">saeva indignatio</span> of the satirists, the follies of dissolute youth are what I take to be the principal subject of this puzzling sheet which, as Griffiths notes, 'has a complicated ancestr'.<sup>i</sup>  </p>

        <p class="pb-2">The costume of the figures would seem to date to the 1620s, and this agrees with the known dates of activity of the engraver, Martin Droeshout, who has signed the sheet with his monogram: <span class="italic">MD sculpsit</span>. The earliest state of the present print to survive, however, was probably issued in the 1650s, bearing Peter Stent's imprint alone, and is held in the Wellcome Institute collection. <sup>ii</sup>  </p>

        <p class="pb-2">The composition derives from a print engraved by Matthaus Greuter, probably c.1600 (itself deriving from one of the emblems in the de Brys' hugely influential <span class="italic">Emblemata Saecularia</span> of 1596), which was issued in French/German and German-only editions, the former bearing the title <span class="italic">Le medecin guarissant Phantasie Purgeant aussi Par drogues la folie</span> [The doctor curing fantasies, and also purging folly with drugs]. But Droeshout made significant changes to his model, dropping the doctor's assistant, adding a richly-dressed couple, an inset panel in which two pluralists confront each other weighed down by the churches on their shoulders, and a great deal of explanatory text in the form of labels within the image-frame.</p>

        <p class="pb-2">Naming the wonder-working doctor <span class="italic">Panurgus</span> seems to have been Droeshout's innovation. Why? It is unlikely Droeshout had read Rabelais - most English intellectuals knew only the French author's name, which they used, like those of Aretino and Machiavelli, merely as a hate-word. <span class="italic">Panourgia</span> is a medical term, and Galen uses it for 'adulterated or false drugs', and although the etymological sense of the name is neutrally 'all-work', later English usage similarly tended to interpret the term pejoratively as 'ready to do any work', i.e. including illegal things, as a criminal would be. Notwithstanding this, however, there is no doubt that in our print Dr. Panurgus is a <span class="italic">positive</span> figure, able to cure his patients, who come from all ranks of society - as the verses and the figures themselves make clear - of their manifold follies. Significantly, for dating purposes, the Latinate form of the name - which by itself suggests independence of Rabelais' creation - is known to have been used by two English writers in 1619 and 1623 only, and perhaps strengthens the case for an origin in the 1620s. <sup>iii</sup>  </p>

        <p class="pb-2">From the copious inscription text I excerpt a few salient points.</p>

        <p class="pb-2">Though concentrating on the foibles and follies of the gallant, who is by definition youthful, the verse makes clear that the <span class="italic">Millions</span> who resort to the Doctor come from <span class="italic">Cuntry, Citty & the Court</span> - i.e. that folly is no respecter of a person's rank or origins. By his Waters <span class="italic">Drugges ,Conserves & Potions</span>, the Doctor <span class="italic"></span>purgeth fancies follies, Idle motions, many of which are detailed in the verses below the image, but also visualised in the phantasmagoria that escapes from the <span class="italic">Gallants Fornace</span>. The Doctor is currently pouring a dose labelled <span class="italic"></span>Wisdome and Understanding down the throat of a <span class="italic">rude Rustical</span>l who sits on a close-stool and through whom <span class="italic">passe</span> various animals and birds, including an ass, which is being milked by a man (in the German original only three little fools are excreted). The Doctor informs us that taking the <span class="italic">Gallants Braine</span> out and washing it had proved ineffective, but now, subliming his head in the furnace has yielded <span class="italic">good Successe</span> - in the form of the <span class="italic">Strange Chimaera-Crotchetts</span> visible in the smoke above. They are later referred to as both <span class="italic">Projects and ayrie Castles</span> - i.e. 'castles in the air', cf. from Burton's contemporary <span class="italic">Anatomy of Melancholy</span> (1621): <span class="italic">That castle in the ayr, that crochet, that whimsie <sup>iv</sup></span>  - and include cards, dice, backgammon-board, tobacco-pipes, violin, tennis, masks, feathers, plumed hat, swords, a dressed ape, a woman with a fan, a man teaching a horse to perform tricks, a bear-baiting, a boy flying a kite, a man with wings strapped to his shoulders (attempt at human flight? No Elizabethan/Jacobean candidate known to me, though doubtless they existed - for a while...), a man sliding down a rope from the tower of (Old) St. Paul's (a popular contemporary feat), jug and goblet, limed branch for bird-catching, a man walking a pair of hounds, and a fencer (with wings). Most of these are copied from the German original - the interest for us lies chiefly in those which were not, and which we may thus reasonably consider peculiarly English: they include the bear-baiting, the tobacco-pipes, <sup>v</sup>   the St. Paul's rope-slider and the horse being taught tricks - perhaps intended to be William Banks and his celebrated horse 'Marocco'. </p>

        <p class="pb-2">To the well-dressed lady with her fan and pet squirrel on a lead the Doctor says,</p>

        <p class="pb-2">
        Once (faire) I knew the tongues Phlebotomie<br>
        Had powre to Cure your Sexes Maladie<br>
        But now youre manly humors boile so highe<br>
        That you must in the Gallants Fornace lye
        </p>

        <p class="pb-2">which looks like a swipe at both the garrulity traditionally attributed to women, as well - interestingly - as evidencing a more contemporary Jacobean concern with manly women, as reflected for instance in the pamphlet, <span class="italic">Hic Mulier: or, The Man-Woman: Being a medicine to cure the Coltish Disease of the Staggers in the Masculine-Feminines of our Times </span>(1620). The verses claim she has even more <span class="italic">wandring Crotchets</span> than the gallant and lists them:</p>

        <p class="pb-2">
        Steelettoes <sup>vi</sup>  girdles patches painted brests <sup>vii</sup><br>
        Points <sup>viii</sup>  powders feathers washes <sup>ix</sup>  & the rest<br>
        ... Haire breath Complexions all are borrowed ware.
        </p>

        <p class="pb-2">The similarly well-dressed gentleman the Doctor addresses mockingly as <span class="italic">Sir Briske, spruce master Cittyzsinne</span>, in which the last, clearly eccentric spelling is presumably to point up a pun, as if he embodies the <span class="italic">City's sin</span>. <sup>x</sup>  Though, like his lady, he looks more like a courtier than a tradesman - which has led to much complicated historical speculation, principally that the pair are the Countess of Essex and her lover the Earl of Somerset - the contemporary label identifies one of the ingredients of the potion the Doctor will administer as <span class="italic">plaine dealing</span>, and one of the things it will expel as <span class="italic">couzening weights</span>. But perhaps it is precisely part of this upstart citizen's presumption that, though a mere tradesman, he apes the dress of the gentry. In the probably contemporary sheet, <span class="italic">The Common Weales Canker Worms or the Locusts both of Church, and States</span>, the Merchant similarly says, <span class="italic">I cosen these foure</span> [i.e the four preceding characters] and his inset emblem is a pair of scales and a yardstick with the motto, <span class="italic">libris et labris lucror furtiuis</span> [I gain by fraudulent scales and measures], while in the verse beneath the engraving, he says, <span class="italic">Ile ease your purses with a trick of skill/While mine with waights & measures false I fill</span>.</p>

        <p class="pb-2">Inset into our engraving is a panel - seemingly not a later addition - depicting two divines confronting each other, both with churches on their backs which cause them to stoop, and one of whom carries a second church in his arms. It is tempting to suggest that the square frame in which these pluralists are depicted, and which appears to interrupt the composition, is a post-Droeshout interpolation, perhaps c.1642, when the motif was current (and which is also the earliest known date of Peter Stent's activity as a print-publisher). <sup>xi</sup>  </p>

        <p class="pb-2">It is captioned</p>

        <p class="pb-2">
        Who bore two Churches & complaind of none<br>
        Nowe being purged findes too much of one.  
        </p>

        <p class="pb-2">If this pluralists panel is original, however, we can certainly point to the existence of the iconographic motif this early, for it features prominently in <a href="#">The Mappe of the Man of Sinne</a> issued in 1622. Indeed, it might provide a useful dating indication for the <span class="italic">Panurgus</span> print, as the <span class="italic">Nowe</span> of the caption perhaps suggests some topical ecclesiastical legislation.</p>

        <p class="pb-2">There may well be a 'quotation' from <span class="italic"></span>Doctor Panurgus in a print issued some fifty years later entitled <a href="#">The Committee; or Popery in Masquerade (1680)</a>. This holds up the various nonconformist sects for ridicule, including the Quakers; they are depicted as constituting a committee listening to petitions from a dog, a horse, a man and a woman. To the left, the various victims of the Civil War appear in chains, while on the right, a Church of England priest is forced to vomit forth his living - a detail which may well owe something to the fact that its designer, Sir Roger L'Estrange, the most effective of the Tory propagandists at this period, was also the Restoration censor, and in that capacity had personally accorded <span class="italic">Doctor Panurgus</span> his imprimatur eight years earlier; indeed, the present impression (one of only three known) is signed and dated by him, <span class="italic">Licensed October 28 1692, Ro. L'estrange</span>.</p>

