        <span class="italic">A picture of the Ridinge of the Asse</span> was licensed to Henry Roberts on 7th March 1607 according to the  <span class="italic">Stationers' Registers</span>, thus providing a very precise date for the fine print previously attributed by A.M. Hind to an 'Anonymous Engraver, c.1600-10', <sup>i</sup>and surviving only in a later impression in the British Museum and the Houghton Library, Harvard, with the address of the late seventeenth-century publisher, John Garrett. Recently Antony Griffiths firmly attributed it to the engraver Renold Elstrack, born c.1570 and last heard of in 1625. He further noted that the printseller Thomas Jenner was advertising it as part of his stock in 1662 as <span class="italic">The Ridinge of the Asse</span>, and that it must have passed, on his death eleven years later, to Garrett (as did most of the rest of Jenner's stock). As Griffiths cogently remarks, 'That only two impressions survive of a print that remained in production for so long is entirely typical of early British printmaking'. <sup>ii</sup>Elstrack is also known as the engraver of at least one other broadside satire, the traditional misogynist <span class="italic">Bulchin</span> and <span class="italic">Thingut</span>.<sup>iii</sup></p>
        
        <p class="py-2">Perhaps not altogether surprisingly, given what little we know of Elstrack's family origins, his engraving derives quite closely from an early sixteenth-century German woodcut of which an impression survives in the Douce print collection at the Ashmolean Museum, Oxford, though not the speeches given to the various representatives of society who struggle to ride the ass. <sup>iv</sup>Evidently a fairly well-known motif in Germany, it is found, for example, on one of the biscuit-moulds (dated 1517) belonging to Claus Stalburg the Rich, Mayor of Frankfurt, who catalogued it thus: <span class="italic">Der 16. stain ist das eselryten, und will eyn iglicher der allernest seyn</span> [The 16th stone (biscuit-mould) is Riding the Ass, and everyone wants to be the very next].<sup>v</sup></p>

