        </p>

        <p class="py-2">
        The German woodcut print is untitled, but the full title of Elstrack's version - <span class="italic">Whilst maskinge in their follies all doe passe/ Though all say nay yet all doe ride the asse -</span> probably accurately reflects the intention of the original composition, i.e. it is a general, rather than a particular, satire, in which, with the exception of the judge (<span class="italic">Mr Justice</span> - though the verse explains that his motives for declining to ride are selfish), all social ranks and types of early Jacobean society (including the prostitute <span class="italic">Dame Punke, Don Pandar, Don Gull and a Gallant</span>) are associated with asinine folly (cf. the similar message of <span class="italic">Doctor Panurgus</span>, which will be discussed next month). All 'ride the ass', that is, everyone is foolish, though they even squabble about this. With this usage, compare the title-page woodcut to <span class="italic">The Fool's Complaint to Gotham College</span> (1643) depicting a fool riding on an ass which says, <span class="italic">The fool rides me</span>. Though seemingly unrecorded elsewhere, Robert Burton appears to use the idiom in precisely the same sense and context in his vastly learned <span class="italic">Anatomy of Melancholy (1621): that they may go 'ride the ass', and all sail along ... in the 'ship of fools'.<sup>vi</sup></span> Burton shows an awareness of pictorial prints throughout his book, referring earlier in passing to the fact that <span class="italic">all the world is mad... it is (which Epicthonius Cosmopolites expressed not many years since in a map) made like a fool's head (with that motto, Caput helleboro dignum).<sup>vii</sup></span> </p>

        <p class="py-2">A late seventeenth- or perhaps early eighteenth-century engraver revived and revised Elstrack's composition to illustrate another sheet entitled, <span class="italic">The ass age, or the world in hieroglyphick. An amusement, agreeably resembling the humour of the present times.</span> Interestingly an earlier state, which must have been pirated, is suggested by the words <span class="italic">Beware of paultry wooden-cutts</span>  after the imprint, and, indeed, a woodcut-illustrated version survives in the Folger Shakespeare Library, entitled <span class="italic">The ass age: or, the fools in fashion: being a comical description of the times</span>, dated 1712. </p> 

      
